body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  box-sizing: border-box;
  cursor: default;
  color: #222; }

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  font-size: 0.875em;
  font-family: "Source Code Pro", monospace, sans-serif;
  font-weight: 400;
  box-sizing: inherit; }

#root {
  width: 100vw;
  height: 100vh; }

.App {
  text-align: center;
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  width: 95vw;
  height: 100vh;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem;
  background-color: ghostwhite;
  box-shadow: 0px 0px 15px 1px rgba(100, 100, 100, 0.3);
  margin: 0 auto; }

h1 {
  font-size: 5.9vw; }

h2 {
  font-size: 3vh; }

#section1 {
  color: #fff; }

.welcome {
  background-color: red; }
  .welcome img {
    height: 50vh;
    margin-bottom: 5vh; }

.fp-section {
  text-align: center; }

button {
  padding: 0.93em 1.87em;
  background: #35495e;
  border-radius: 5px;
  border-color: transparent;
  display: block;
  color: #fff;
  margin: 0 auto;
  cursor: pointer;
  font-size: 0.85em; }

.menuItem {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh; }
  .menuItem__signature {
    background-image: url(/easeupmenu/static/media/signature.492ce855.jpg); }
  .menuItem__freshYuZu {
    background-image: url(/easeupmenu/static/media/freshYuZu.4f5d9059.jpg); }
  .menuItem__oolongIcedTea {
    background-image: url(/easeupmenu/static/media/oolongicedtea.b5759f27.jpg); }
  .menuItem__matchaLatte {
    background-image: url(/easeupmenu/static/media/matchaLatte.b2cfe922.jpg); }
  .menuItem__kuromitsu {
    background-image: url(/easeupmenu/static/media/kuromitsu.7f604190.jpg); }
  .menuItem__elegantMatcha {
    background-image: url(/easeupmenu/static/media/elegantMatcha.7c91c591.jpg); }
  .menuItem__darkChoco {
    background-image: url(/easeupmenu/static/media/darkChoco.5a47e4e0.jpg); }
  .menuItem__peachTea {
    background-image: url(/easeupmenu/static/media/peachTea.48072b60.jpg); }
  .menuItem__oolongPeace {
    background-image: url(/easeupmenu/static/media/oolongPeace.0938c296.jpg); }
  .menuItem__strawberryLimeSoda {
    background-image: url(/easeupmenu/static/media/strawberryLimeSoda.6717871c.jpg); }
  .menuItem__miloMagma {
    background-image: url(/easeupmenu/static/media/MiloLava.dc58be5e.jpg); }

.item-detail {
  font-size: 4vh;
  color: #d3d3d3;
  padding: 4vh; }
  .item-detail__darkChoco {
    font-size: 5vh; }
  .item-detail__oolong {
    font-size: 3vh; }

.item-price {
  color: #d3d3d3; }
  .item-price h1 {
    font-size: 5vh;
    padding: 6vh;
    margin-bottom: 2vh; }
  .item-price p {
    font-size: 5vh;
    padding: 6vh; }
    .item-price p span {
      font-size: 3vh; }

.notification {
  font-size: 5vh;
  color: #d3d3d3;
  padding: 6vh; }
  .notification a {
    color: #d3d3d3; }

.usage {
  font-size: 5vh;
  color: #222;
  padding: 6vh;
  display: grid;
  grid-template-columns: 2fr 1fr 2fr;
  height: 100vh;
  -webkit-align-items: center;
          align-items: center; }
  .usage__leftArrow img {
    width: 50%;
    -webkit-filter: opacity(0.5) drop-shadow(0 0 0 lightGray);
            filter: opacity(0.5) drop-shadow(0 0 0 lightGray); }

.slideHeader {
  font-size: 3vh;
  color: #d3d3d3;
  display: -webkit-flex;
  display: flex;
  padding: 3vh;
  position: absolute;
  top: 2px;
  background-color: rgba(10, 10, 10, 0.5); }

